<template>
  <div class="goodsContent">
    <div class="container">
      <div class="row" v-for="item in allGoods">
        <h3 class="typeName">{{item.type.name}}<span>{{item.type.intro}}</span></h3>
        <div class="onegoods col-sm-5" v-for="one in item.goods">
          <div class="imgContent">
            <img class="goodsPic" :src=one.img>
          </div>
          <div class="goodsAbout">
            <h3 class="goodsName">{{one.name}}</h3>
            <span class="sales">{{one.sales}}</span>
            <div class="priceContent">
              <span>￥{{one.price}}</span>
              <span><a href="javascript:void(0)" @click="addCar(one)" class="addCarBtn btn btn-primary">添加购物车</a></span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="goodsCar">
    <div class="goodsCarShow">
      <div class="clearCar">
        <span><a href="javascript:void(0)" @click="clearCar()">清空</a></span>
      </div>
      <ul>
        <li v-for="(goods,index) in goodsCar">
          <span>{{goods.name}}</span>
          <span class="priceColor">{{goods.price}}￥</span>
          <span><a href="javascript:void(0)" @click="deleteGoods(index)">删除</a></span>
        </li>
      </ul>
    </div>
    <div class="pay">
      <span class="totalPrice">总共￥{{computePrice}}</span>
      <span class="payBtn"><a href="javascript:void(0)" @click="toMyOrderPage()">下单</a></span>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  beforeCreate () {
    this.$http.get('http://127.0.0.1:3000/users/getAllGoods').then((success) => {
      this.allGoods = success.body.result
    }, (err) => {
      console.log(err)
    })
  },
  data () {
    return {
      allGoods: [],
      goodsCar: []
    }
  },
  methods: {
    addCar: function (goods) {
      this.goodsCar.push(goods)
    },
    deleteGoods: function (index) {
      this.goodsCar.splice(index, 1)
    },
    clearCar: function () {
      this.goodsCar = []
    },
    toMyOrderPage: function () {
      this.$http.post('http://127.0.0.1:3000/users/toMyOrderPage', {'orders': this.goodsCar}).then((success) => {
        if (success.body.result === '1') {
          this.$router.push('/toMyOrderPage')
        } else {
          window.location = 'http://127.0.0.1:3000/users/login'
        }
      }, (err) => {
        console.log(err)
      })
    }
  },
  computed: {
    computePrice: function () {
      let totalPrice = 0
      this.goodsCar.forEach(function (item) {
        totalPrice = totalPrice + item.price
      })
      return totalPrice
    }
  }
}
</script>

<style scoped>
.goodsContent{
  width: 100%;
}
.row{
    width: 75%;
   /*border:1px solid red;*/
   position: relative;

   margin: 10px;
}
.imgContent{
  float: left;
  width: 100px;
  height: 100px;
  display: inline-block;
}
.goodsPic{
  width: 100px;
  height: 100px;
}
.goodsName{
  display: block;
  padding: 0px 10px;
}
.goodsAbout{
  padding: 5px;
  height: 100%;
  margin-top: 0;
  display: inline-block;
}
.sales{
  display: block;
  padding: 10px;
}
.priceContent{
  padding: 10px;
  width: 100%;
}
.typeName{
  padding: 10px;
}
.addCarBtn{
  position: absolute;
  right: 10px;
  bottom:10px;
}
.onegoods{
  margin: 10px;
  background-color: #fff;
  border:1px solid #ccc;
}
.typeName span{
  font-size: 13px;
}
.goodsCar{
  position: fixed;
  right: 0;
  bottom: 0;
  width: 320px;
  /*background: blue;*/
}
.pay{

  display: flex;
  width: 100%;
  height: 50px;
  justify-content: space-between;
}
.goodsCarShow li{
  height: 50px;
  display: flex;
  justify-content: space-around;
  line-height: 50px;
  border-top: 1px solid #ccc;
}
.totalPrice{
  background-color: #2c2c2c;
  width: 200px;
  font-size: 20px;
  text-align: center;
  line-height: 50px;
  color: #fff;
}
.payBtn{
  width: 120px;
  background-color: #51d862;
  font-size: 20px;
  text-align: center;
  line-height: 50px;
}
.priceColor{
  color: red;
}
.goodsCarShow{
  border-left: 1px solid #ccc;
}
.clearCar{
  height: 30px;
  border-top: 3px solid #0089dc;
  line-height: 30px;
  padding-left: 20px;
}
</style>



