<template>
  <div class="header">
    <div class="container">
      <h1><a href="/users">优优</a></h1>
      <a href="/users">首页</a>
      <router-link tag="a" to="/toMyOrderPage">
        我的订单
      </router-link>
      <nav class="mine">
        <div class="loginState" v-show="userLogin==1">
          <span><a href="http://127.0.0.1:3000/users/login">欢迎{{userName}}</a></span>
          <span>/</span>
          <span><router-link tag="a" to="/toMyOrderPage">我的资料</router-link></span>
          <span><a href="http://127.0.0.1:3000/users/loginOut">退出登录</a></span>
        </div>
        <div class="loginOutState" v-show="userLogin==0">
          <span><a href="http://127.0.0.1:3000/users/login">登录</a></span>
          <span>/</span>
          <span><a href="http://127.0.0.1:3000/users/register">注册</a></span>
        </div>
      </nav>
    </div>
  </div>
</template>

<script>
  export default {
    beforeCreate () {
      this.$http.get('http://127.0.0.1:3000/users/getLoginState').then((success) => {
        this.userLogin = success.body.userLogin
        this.userName = success.body.userName
      }, (err) => {
        console.log(err)
      })
    },
    data () {
      return {
        userLogin: 0,
        userName: null
      }
    }
  }
</script>

<style scoped>
  .header{
    width: 100%;
    height: 60px;
    background-color: #1e89e0;
    color: #fff;
  }
  .container{
    width: 960px;
    margin: 0 auto;
    height: 100%;
    line-height: 60px;
  }
  h1 a{
    font-size: 30px;
    display: inline-block;
    height:100%;
    float: left;
    margin-right: 20px;
    color: #fff;
  }
  .container > a{
    float: left;
    display: inline-block;
    height:60px;
    width: 100px;
    text-align: center;
    color: #fff;
    font-weight: 400;
    font-size:16px;
  }
  .mine{
    float: right;
  }
  .mine a{
    color: #fff;
  }
</style>



