import Vue from 'vue'
import Router from 'vue-router'
import Comments from './../components/content/comments.vue'
import GoodsContent from './../components/content/goodsContent'
import MyOrderPage from './../components/content/myOrder'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'goodsContent',
      component: GoodsContent
    },
    {
      path: '/comments',
      name: 'comments',
      component: Comments
    },
    {
      path: '/toMyOrderPage',
      name: 'MyOrderPage',
      component: MyOrderPage
    }
  ]
})



