<template>
  <div class="goodsContent">
    <div class="container">
      <div class="row">
        <div class="left col-sm-2 col-sm-offset-1">
          <ul>
            <li @click="flag='isOrders'">我的订单</li>
            <li @click="flag='isPassword'">修改密码</li>
            <li @click="flag='isAddress'">修改地址</li>
          </ul>
        </div>
        <div class="content col-sm-7">
          <div v-show="'isOrders'==flag" class="myOrders">
            <div class="row" v-for="one in myOrders">
              <div class="order">
                <span>{{formateDate(one.date)}}</span>
                <span>{{one.goods[0].name}}</span>
                <span>￥{{one.goods[0].price}}</span>
                <span>{{one.state?'已处理':'未处理'}}</span>
                <a href="javascript:void(0)" @click="comment(one.goods[0]._id)">评论</a>
              </div>
            </div>
          </div>
          <div v-show="'isPassword'==flag" class="modifyPassword">
            <form class="form-horizontal">
              <div class="form-group">
                <div class="col-sm-3">
                  <p class="warn" v-show="isShow=='modifyResult'">{{warnText}}</p>
                </div>
              </div>
              <div class="form-group">
                <label for="oldpassword" class="col-sm-4 control-label">原始密码</label>
                <div class="col-sm-3">
                  <input type="password" v-model="oldpassword" class="form-control" id="oldpassword" placeholder="原始密码">
                </div>
                <p class="warn" v-show="isShow=='oldpassworderr'">{{warnText}}</p>
              </div>
              <div class="form-group">
                <label for="password" class="col-sm-4 control-label">密码</label>
                <div class="col-sm-3">
                  <input type="password" v-model="password" class="form-control" id="password" placeholder="密码">
                </div>
                <p class="warn" v-show="isShow=='passworderr'">{{warnText}}</p>
              </div>
              <div class="form-group">
                <div class="col-sm-offset-4 col-sm-2">
                  <a id="modifyPassword" @click="modifyPassword()" class="btn btn-default">修改</a>
                </div>
              </div>
            </form>
          </div>
          <div v-show="'isAddress'==flag" class="modifyAddress">
            <form class="form-horizontal">
              <div class="form-group">
                <label for="password" class="col-sm-4 control-label">地址</label>
                <div class="col-sm-8">
                  <textarea rows="6" id="address" v-model="myAddress"></textarea>
                </div>
              </div>
              <div class="form-group">
                <div class="col-sm-offset-4 col-sm-2">
                  <a id="modifyPassword" @click="modifyAddress()" class="btn btn-default">修改</a>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
    <div class="commentForm" v-show="formShow">
    </div>
    <div class="mymodal" v-show="formShow">
      <form class="form-horizontal">
        <div class="form-group">
          <label for="phone" class="col-sm-3 control-label">评论</label>
          <div class="col-sm-3">
            <textarea v-model="comments" rows="5"></textarea>
          </div>
        </div>
        <div class="form-group">
          <div class="col-sm-offset-6 col-sm-6">
            <a id="sureBtn" class="btn btn-default" @click="submitCom">确定</a>
            <a id="sureBtn" class="btn btn-default" @click="cancelCom">取消</a>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  beforeCreate () {
    this.$http.get('http://127.0.0.1:3000/users/getMyOrder').then((success) => {
      if (success.body.result === -1) {
        window.location = 'http://127.0.0.1:3000/users/login'
      } else {
        this.myOrders = success.body.result
        this.myAddress = success.body.address
      }
    }, (err) => {
      console.log(err)
    })
  },
  data () {
    return {
      isShow: '',
      warnText: '',
      flag: 'isOrders',
      myOrders: [],
      myAddress: '',
      id: '',
      formShow: false,
      comments: '',
      oldpassword: '',
      password: ''
    }
  },
  methods: {
    formateDate: function (date) {
      let adate = new Date(date)
      let dateString = adate.getFullYear() + '/' + (adate.getMonth() + 1) + '/' + adate.getDate()
      return dateString
    },
    comment: function (_id) {
      // this.$http.post('http://127.0.0.1:3000/users/comment').then((success) => {
      //   console.log(success)
      // }, (err) => {
      //   console.log(err)
      // })
      this.id = _id
      this.formShow = true
    },
    submitCom: function () {
      if (this.comments === '') {
        this.id = ''
        this.formShow = false
        return
      } else {
        this.$http.post('http://127.0.0.1:3000/users/comment', {'comment': this.comments, 'id': this.id}).then((success) => {
          console.log(success)
          this.id = ''
          this.formShow = false
          this.comments = ''
        }, (err) => {
          console.log(err)
          this.id = ''
          this.formShow = false
        })
      }
    },
    cancelCom: function () {
      this.formShow = false
      this.id = ''
    },
    modifyPassword: function () {
      if (this.oldpassword === '' || this.oldpassword === null) {
        this.isShow = 'oldpassworderr'
        this.warnText = '*不能为空！'
        return
      } else if (this.password === '' || this.password === null) {
        this.isShow = 'passworderr'
        this.warnText = '*不能为空！'
        return
      } else {
        this.isShow = ''
        this.$http.post('http://127.0.0.1:3000/users/modifyPassword', {'oldpassword': this.oldpassword, 'password': this.password}).then((success) => {
          if (success.body.result === '1') {
            this.isShow = 'modifyResult'
            this.warnText = '修改成功'
            let _this = this
            setTimeout(function () {
              _this.isShow = ''
              _this.warnText = ''
            }, 1000)
          }
        }, (err) => {
          console.log(err)
        })
      }
    },
    modifyAddress: function () {
      this.$http.post('http://127.0.0.1:3000/users/modifyAddress', {'address': this.myAddress}).then((success) => {
        console.log(success)
      }, (err) => {
        console.log(err)
      })
    }
  }
}
</script>

<style scoped>
.left{
  /*border:1px solid red;*/
  height: 400px;
  border-right: 1px solid #ccc;
}
.left li{
  padding: 20px;
  color: #1e89e0;

}
.left li:hover{
  cursor: pointer;
}
.content{
  /*border:1px solid blue;*/
}
.order{
  display: flex;
  justify-content: space-around;
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #ccc;

}
.row{
  background-color: #fff;
}
.commentForm{
  position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000;
    opacity: 0.2;
}
.mymodal{
   width: 300px;
   height: 200px;
   position: absolute;
   margin: auto;
   left: 0;
   right: 0;
   top: 0;
   bottom: 0;
   z-index: 9999999;
   background-color: #fff;
   border-radius: 10px;
   padding:20px 0;
}
.sureBtn{
  float: right;
}
</style>



