<template>
  <div class="comments">
	  <div class="container">
	  	<div class="warp row">
        <div class="commentWarp col-sm-7 col-sm-offset-2" v-for="comment in allComments">
          <span><span class="yonghu">用户</span>{{comment.user[0].username}}</span>
          <span class="mainComment">{{comment.comment}}</span>
          <span class="date">{{formateDate(comment.date)}}</span>
        </div>  
      </div>
	  </div>
  </div>
</template>

<script>
export default {
  beforeCreate () {
    this.$http.get('http://127.0.0.1:3000/users/getComments').then((success) => {
      this.allComments = success.body.result
    }, (err) => {
      console.log(err)
    })
  },
  data () {
    return {
      allComments: []
    }
  },
  methods: {
    formateDate: function (date) {
      let adate = new Date(date)
      let dateString = adate.getFullYear() + '/' + (adate.getMonth() + 1) + '/' + adate.getDate()
      return dateString
    }
  }
}
</script>

<style scoped>
.comments{
  background: #fff;
}
.commentWarp{
  min-height: 100px;
  border:1px solid #ccc;
  padding: 20px;
}
.yonghu{
  color: #51d862;
}
.mainComment{
  position: absolute;
  margin-top: 20px;
}
.date{
  float: right;
  top: 10px;
}
</style>



